#hero-root{
	overflow: hidden;
	padding: 0;
}

.hero-container{
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: center;
	height: 100%;
	padding: 0 1rem !important;
	margin: 0 auto !important;
}

.hero-top{
	display: flex;
	justify-content: space-between;
	align-items: center;
	height: 100%;
}

.hero-container_top-left{
	height: 100%;
	flex: 1;
	display: flex;
  flex-direction: column;
	justify-content: center;
	align-items: flex-start;
	
}

.flipword-wrapper{
	display: flex;
	align-items: flex-end;
}

.hero-container_top-right{
	flex:0.8;
	height: 420px;
	display: flex;


	// column-gap: 2rem;
	// position: absolute;
	// z-index: -2;
	// right: 0;
	// 3D config
	// perspective: 300px;
	// overflow-y: hidden;

	// transform: translateY(-50px);

}

.large{
	font-size: var(--h1-font-size);
	font-weight: var(--h1-font-weight);
	line-height: var(--h1-line-height);
}


// marquee component
.marquee-container{
	height: 100%;

	display: flex;
	justify-content: center;
	align-items: center;
	flex: 1;

	box-sizing: border-box;
	position: relative;

	// 3D config
	transform-style: preserve-3d;
	transform: 
		translate(-100px, 0px) translateZ(-100px) 
		rotateX(22deg) rotateY(-10deg) rotateZ(20deg)
	;

}

.marquee-content-wrapper{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	row-gap: 1rem;
	column-gap: 1rem;
}

.marquee-content{
	display: flex;
	justify-content: center;
	align-items: center;
	row-gap: 1rem;
	column-gap: 1rem;
	
	flex-direction: column;
	animation: marquee 30s linear infinite;
	flex-shrink: 0;

}

.marquee-content span{
	display: inline-block;
	margin: 1rem 0rem;
	font-size: 1.2em;
	color: #FFF
}

@keyframes marquee {
	0% {
		transform: translateY(0%);
	}

	100% {
		transform: translateY(calc(-100% - 1rem));
	}
}


.marquee-top-gradient,
.marquee-bottom-gradient{
	position: absolute;
	width: 100%;
	height: 300px;
}

.marquee-top-gradient{
	background: linear-gradient(
		to bottom,
		#030412 0%,
		rgba(3, 4, 18, 0.8) 35%,
		rgba(3, 4, 18, 0) 100%
	);
	top:0;
	z-index: -1;
}

.marquee-bottom-gradient{
	background: linear-gradient(
		to top,
		#030412 0%,
		rgba(3, 4, 18, 0.8) 50%,
		rgba(3, 4, 18, 0) 100%
	);
	bottom: 0;
	z-index: -1;
}

// card
.card{
	padding: 1rem;
	border-radius: 15px;
	border: 1px solid color-mix(in oklab, var(--color-gray-50) 10%, transparent);
	background-color: var(--color-indigo);
}

// AnimatedHeader
.animated-header_container{

	width: 100%;

	background: linear-gradient(
		to top,
		#030412 0%,
		rgba(3, 4, 18, 0.8) 40%,
		rgba(3, 4, 18, 0) 100%
	);
}

.animated-header_subtitle{
	margin: 0;
}

.animated-header_title_wrapper{
	box-sizing: border-box;
	overflow: hidden;
}

.animated-header_title{

	font-size: calc(1.7 * var(--h1-font-size)) !important;
	margin: 0 0 -31px 0;
	text-transform: uppercase;

	&_br{
		position: absolute;
		width: 100%;
		height: 2px;
		background-color: #FFF;
		left: 0;
		top: 0;
	}

}

.animated-header_container-bottom{
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: flex-end;
}

.animated-header_description-item{
	display: inline-block;
	margin: 0.5rem 0;
	
}


// particles
.particles-container{
	position: absolute;
	inset: 0;
	z-index: -50;
}


